@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.3s ease-in;
}

body {
  background: #000;
}
::-webkit-scrollbar {
  background-color: transparent;
  width: 10px;
}
::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background: linear-gradient(#222, #333);
}
label {
  font-family: "Fira Sans";
}
a {
  text-decoration: none;
  transition: all 0.2s ease-in;
  font-family: "Fira Sans", sans-serif;
}
button,
a {
  cursor: pointer;
}
hr {
  background: #222;
  border-color: #222;
}
input {
  font-family: "Fira Sans", sans-serif;
}

.navbar {
  width: 100%;
  position: fixed;
  top: 0;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
  padding: 12px;
  transition: all 0.5s ease-in;
}
.navbar__black {
  background-color: black;
}
.nav-links {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 15px;
  list-style: none;
  z-index: 70;
}
.nav-link {
  color: #555;
}
.nav-link-active {
  color: #fff;
}
.nav-link:hover {
  color: #fff;
}
.nav-icon {
  display: none;
  font-size: 22px;
  cursor: pointer;
}
.nav-icon path {
  color: #666;
}
@media only screen and (max-width: 600px) {
  .nav-links {
    display: flex;
    flex-direction: column;
    position: fixed;
    text-align: center;
    width: 100%;
    top: 40px;
    left: -100%;
    transition: 0.8s all;
    padding: 20px 80px 0 80px;
  }

  .nav-item {
    padding-bottom: 3px;
    border-bottom: 1px solid #222;
  }

  .nav-links.active {
    background: black;
    left: 0;
    padding-top: 20px;
    height: 230px;
  }

  .nav-icon {
    display: flex;
  }
}

#home {
  position: relative;
  top: 0;
  left: 0;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#home::after {
  content: "";
  opacity: 0.3;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
  background: url(/portfolio/static/media/banner.ace49dde.jpg);
}
.header__text {
  margin: 20px 0;
}
.header__title {
  font-size: 60px;
  line-height: 50px;
  font-weight: 100;
  font-family: "Cinzel";
}
.header__description {
  font-size: 15px;
  margin: 20px 0;
  font-weight: 100;
  font-family: "Cinzel";
}
.header__buttons {
  margin-bottom: 50px;
}
.header__button {
  padding: 15px 35px;
  margin: 3px;
  color: #aaa;
  border: 1px solid #333;
  transition: all 0.3s ease-in;
}
.btn-icon {
  margin-right: 10px;
}
.btn-icon path {
  color: #aaa;
  transition: all 0.3s ease-in;
}
.header__button:hover > .btn-icon path {
  color: #fff;
}
.header__button:hover {
  background: rgba(200, 200, 200, 0.2);
  border-color: #fff;
  color: #fff;
}
@media only screen and (max-width: 900px) {
  .header__button {
    padding: 12px 20px;
    font-size: 15px;
  }
  .header__title {
    font-size: 45px;
  }
  .header__description {
    font-size: 12px;
    margin: 10px 0;
  }
}
@media only screen and (max-width: 376px) {
  .header__title {
    font-size: 40px;
  }
}

.footer {
  padding-top: 48px;
  margin-bottom: 48px;
  font-size: 14px;
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Fira Sans", sans-serif;
}
.footer__icon {
  font-size: 25px;
}
.footer__icon path {
  color: #444;
}
.footer__link:hover > .footer__icon path {
  color: #fff;
}

.copyright {
  margin: 0;
  padding: 0 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.footer__name {
  font-size: 15px;
  color: #aaa;
}
.footer__name:hover {
  color: #fff;
}
.social__links {
  margin: 18px 0 30px 0;
  padding: 0;
  width: 100%;
  font-size: 30px;
}
.social__links a {
  margin: 0 5%;
}
.btn__top {
  position: absolute;
  top: -22px;
  width: 45px;
  height: 45px;
  margin: 0 auto;
  background: #000;
  border: 1px solid #333;
  color: #fff;
  font-size: 20px;
}
.btn__top:hover {
  background: rgba(200, 200, 200, 0.2);
  color: #000;
  border-color: #fff;
}

* {
  box-sizing: border-box;
}
.contact {
  padding: 60px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 80vh;
  border-top: 1px solid #444;
}
.contact::after {
  content: "";
  opacity: 0.3;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
  background: url(/portfolio/static/media/banner.ace49dde.jpg);
}
.form {
  padding: 0px 20px;
  width: 100%;
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  justify-items: stretch;
  align-items: stretch;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(1, 400px);
  grid-template-areas: "video_one contact__form video_two";
}
.contact__form {
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-area: contact__form;
}

.form__field {
  margin-top: 30px;
  width: 80%;
  font-size: 20px;
  min-width: 240px;
  text-align: center;
  border: none;
  color: #fff;
  background: transparent;
  outline: none;
  border-bottom: 1px solid #555;
}
.form__field:focus,
.form__field:hover {
  border-bottom: 1px solid #fff;
}
.form__icon {
  margin-right: 10px;
}
.form__icon path {
  color: #aaa;
}
.form__button {
  padding: 10px 20px;
  margin: 30px 0 0 0;
  color: #aaa;
  background: transparent;
  border: 1px solid #333;
}
.form__button:hover {
  background: rgba(200, 200, 200, 0.2);
  border: 1px solid #fff;
  color: #fff;
}
.form__button:hover > .form__icon path {
  color: #fff;
}
.contact-video {
  border-radius: 10px;
}
.video_one {
  grid-area: video_one;
}
.video_two {
  grid-area: video_two;
}
.error {
  margin-top: 5px;
  font-weight: 700;
  color: rgb(253, 66, 66);
}
@media screen and (max-width: 1200px) {
  .form {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
      "contact__form contact__form"
      "video_one video_two";
  }
  .contact {
    height: 100%;
  }
}
@media screen and (max-width: 600px) {
  .contact {
    border: none;
  }
  .form {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    grid-template-areas:
      "contact__form"
      "video_one"
      "video_two";
  }
  .form__field {
    width: 90%;
  }
}

.whatsapp-icon {
  position: fixed;
  z-index: 99;
  font-size: 50px;
  bottom: 30px;
  right: 30px;
}
.whatsapp-icon path {
  color: #25d366;
}
.whatsapp-icon:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
.about {
  width: 100%;
  font-family: "Fira Sans", sans-serif;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 70px 0;
}
.contenedor {
  display: flex;
  width: 70%;
  justify-content: space-between;
  align-items: center;
  height: 80vh;
}
.contenedor__left {
  font-size: 20px;
  flex: 1 1;
}
.contenedor__right {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  background-image: linear-gradient(to right, #000, transparent 70%),
    url(/portfolio/static/media/about.c23d3eb0.jpg);
  width: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  flex: 1 1;
}
.contenedor__right:hover{
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}
.carrousel{
  margin-top: 50px;
  width: 80%;
  display: flex;
  overflow-x: scroll;
}
.carrousel img {
  opacity: .3;
  width: 350px;
  margin: 0 10px;
}
.carrousel img:hover{
  opacity: 1;
}
@media only screen and (max-width: 1039px) {
  .contenedor {
    flex-direction: column;
  }
  .contenedor__left {
    text-align: justify;
    margin-bottom: 15px;
  }
  .contenedor__right {
    width: 100%;
  }
}
@media only screen and (max-width: 500px) {
  .contenedor__left {
    font-size: 13px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.portfolio {
  width: 100%;
  padding: 40px 100px 70px 100px;
}
.portfolio__title {
  line-height: 90px;
  color: #666;
}
.portfolio__title:hover {
  color: #fff;
}
.portfolio__data {
  text-align: center;
}
.portfolio__projects {
  display: flex;
  flex-wrap: wrap;
}
.portfolio__project__card {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.portfolio__project__card > p{
  display:none;
}
.portfolio__project__card:hover > p{
  display: inline;
  display: initial;
}

.portfolio__project {
  flex: 1 1;
  min-width: 300px;
  border: 1px solid #222;
  border-radius: 10px;
  margin: 10px;
  background: transparent;
}
.project-title {
  line-height: 40px;
  color: #666;
  font-weight: 500;
  font-size: 18px;
}
.project-title:hover {
  color: #fff;
}
.portfolio__image__link {
  flex: 1 1;
}
.portfolio__image {
  width: 100%;
  height: 100%;
  object-fit: fill;
  object-position: center center;
  opacity: 0.3;
}
.portfolio__image:hover,
.portfolio__image:focus {
  opacity: 1;
}
img {
  width: 100%;
}
@media only screen and (max-width: 500px) {
  .portfolio {
    padding: 10px;
  }
  .portfolio__title {
    font-size: 17px;
  }
  .project-title {
    line-height: 30px;
    font-size: 17px;
  }
}

.current {
  background: linear-gradient(
      rgba(0, 0, 0, .9),
      rgba(0, 0, 0, .9)
    ),
    url(/portfolio/static/media/dev1.cf277dbe.jpg);
  display: flex;
  font-family: "Fira Sans", sans-serif;
  justify-content: space-evenly;
  align-items: center;
  background-position: center center;
  color: #fff;
  width: 100%;
  height: 80vh;
  font-size: 50px; 
}

.current img {
  width: 500px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}
.resume {
  padding-top: 30px;
  padding-bottom: 0px;
  position: relative;
  font-family: "Fira Sans", sans-serif;
}
.resume::after {
  content: "";
  opacity: 0.3;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
  background: url(/portfolio/static/media/banner.ace49dde.jpg);
}
.education,
.grades,
.skill {
  display: flex;
  justify-content: center;
  align-items: center;
}
.title {
  flex: 1 1;
  text-align: end;
  margin: 50px;
  border-bottom: 1px solid #888;
  font-size: 20px;
  font-weight: 200;
}
.education__items,
.grades__items,
.skills {
  flex: 2 1;
  text-align: start;
  padding: 20px;
  margin: 40px 0;
}
.info {
  margin-bottom: 20px;
}
.info__name {
  font-size: 30px;
}
.info__degree {
  font-size: 13px;
  color: #999;
}
.info__degree__date {
  color: #777;
}
.info__description {
  margin: 10px 0;
  font-size: 14px;
  width: 50%;
}
.skills {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}
.bars,
.bars_language {
  width: 700px;
}
.bar {
  margin: 0 0 3px 0;
  border-radius: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 20px;
}
.bar_language {
  margin: 0 0 20px 0;
  border-radius: 35px;
}
.bar_language > div span {
  color: #000;
}
hr {
  width: 45%;
  border-color: #444;
  margin: 0 auto;
}
@media only screen and (max-width: 1900px) {
  hr {
    width: 70%;
  }
  .bars,
  .bar_language {
    flex-wrap: wrap;
  }
}
@media only screen and (max-width: 900px) {
  .education,
  .grades,
  .skill {
    flex-direction: column;
  }
  .title {
    margin: 50px 50px 10px;
  }
  .info__degree {
    font-size: 16px;
  }
  .info__description {
    font-size: 15px;
    width: 100%;
  }
  .education__items,
  .grades__items {
    text-align: center;
    margin: 0 0 40px 0;
  }
  .bars {
    width: 88%;
    font-size: 13px;
  }
  .bars_language {
    text-align: center;
    width: 100%;
  }
  .bar_language {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
  }
  .skills {
    align-items: center;
  }
  hr {
    width: 85%;
  }
}

